import React, { useMemo } from 'react'
import { AspectRatio } from '@chakra-ui/react'
import { random } from 'lodash'
import { SizeMe } from 'react-sizeme';
import 'fullpage.js/vendors/scrolloverflow'

import Box from '../../components/Box'
import Text from '../../components/Text'
import Container from '../../components/Container'
import BackgroundImage from '../../components/BackgroundImage'
import withData from './data/withData';
import Face from '../Face';

import useResponsive from '../../contexts/mediaQuery/useResponsive';
import theme, { responsive } from '../../components/ThemeProvider/theme';
import useLoader from '../../utils/useLoader';
import imgSize from './data/imgSize'

const idealWidth = 200

const TrashDescription = (props) => (
  <Box.Absolute bottom={responsive('8%', '10%')} right={responsive('18%', '2em')} width={responsive('264%', '99%')} transform="scale(0.33)" transformOrigin="100% 100%">
    <Text textAlign="justify" fontSize={`1.25em`} {...props} />
  </Box.Absolute>
)
const colorsCfg = {
  A: 'green',
  B: 'orange',
  C: 'pink',
}

const TrashPage = ({ data }) => {
  const { isMobile } = useResponsive()
  const colorScheme = `colors.${colorsCfg[data.recycleValue]}`
  const trashWidth = (isMobile ? 150 : 75) * (data.transform.scale ? data.transform.scale / 100 : Math.min(1, idealWidth / (data.xRange[1] - data.xRange[0])))

  const faceId = useMemo(() => random(4) + 1, [data])
  useLoader(data.img)

  const n = `#${String(data.id).padStart(3, '0')}`
  // const bgColor = get(theme, `colors.${colorScheme}`)
  const parts = useMemo(() => {
    if (!data) return null
    return data.imgs.map(({ src }, i) => (
      <Box.FullAbs key={i}>
        <BackgroundImage ratio={imgSize[0] / imgSize[1]} src={src} />
      </Box.FullAbs>
  ))
  }, [data])
  return (
    <Box position="relative" width="100%" height="100%" onClick={() => window.open(`${process.env.PUBLIC_URL}/#/trash/${data.id}`)} cursor="pointer">
      <Box.Absolute left="0.33em" top="0em">
        <Text.Number
          textStroke="0.1rem"
          textStrokeColor={`colors.${colorScheme}`}
          color="white"
          fontSize="3.33em"
        >{n}</Text.Number>
      </Box.Absolute>
      <Box.AbsCenter top={responsive('25%', '50%')} width="100%" textAlign="center" transform="rotate(-12deg)">
        <SizeMe>
          {({ size }) => (
            <Text as="h2" color={colorScheme} fontSize={size.width ? `${Math.min(Math.floor(size.width / (data.name.length + 1)), size.width / 3.5)}px` : 0} fontWeight="900">
              {data.name}
            </Text>
          )}
        </SizeMe>
      </Box.AbsCenter>
      <TrashDescription color={colorScheme} >
        {data.description}
      </TrashDescription>
      <Box.Absolute
        top="0"
        left="0"
        right="0"
        height="100%"
        zIndex="docked"
        pointerEvents="none"
        pt={theme.headerHeight}
      >
        <Container position="relative" height="100%">
          <Box.Absolute
            id="trash-container"
            width={`${trashWidth}%`}
            left={`${(100 - trashWidth) / 2}%`}
            top={responsive('45%', '50%')}
            transform="translate3d(0, -50%, 0)"
          >
            <div>
              <AspectRatio ratio={imgSize[0] / imgSize[1]} overflow="visible">
                <Box overflow="visible">
                  {parts}
                  <Face id={faceId} />
                </Box>
              </AspectRatio>
            </div>
          </Box.Absolute>
        </Container>
      </Box.Absolute>
    </Box>
  )
}

export default withData(TrashPage)
