import imgSize from './data/imgSize'

// const pageCount = 5
const trashSidePos = 20
const scrollingDuration = 1
const idealWidth = 200

let theTimeline
let endTimeline
let progressTimer

const TrashName = ({ children, ...props }) => (
  <Box.Absolute
    top={responsive('6em', '2em')}
    left={responsive('2em', '1em')}
    color="white"
    {...props}
  >
    <Text fontSize={responsive('4em', '2.5em')} fontWeight="900" letterSpacing="0.1em">{children}</Text>
  </Box.Absolute>
)

const SectionTitle = ({ children, ...props }) => (
  <Box.Absolute left={responsive('2em', '1em')} top={responsive('12em', '5.5em')} {...props}>
    <Text fontSize={responsive('2em', '1.5em')}>{children}</Text>
  </Box.Absolute>
)

const TrashDescription = (props) => (
  <Box.Absolute bottom={responsive('8%', '10%')} right={responsive('6%', '6em')} width={responsive('88%', '33%')}>
    <Text textAlign="justify" fontSize={responsive('3em', '1.25em')} {...props} />
  </Box.Absolute>
)

const TrashNumber = ({ children, ...props }) => (
  <Box.Absolute
    bottom={responsive('auto', '2em')}
    top={responsive('2em', 'auto')}
    left={responsive('2em', '1em')}
    color="white"
    {...props}
  >
    <Text.Number fontSize={responsive('3.5em', '1.75em')}>{children}</Text.Number>
  </Box.Absolute>
)

const PageReveal = forwardRef(({ children }, ref) => (
  <Box.Absolute height="0" left="0" right="0" ref={ref} overflow="hidden">
    <FullpageHeight position="absolute" left="0" right="0" bottom="0">
      {children}
    </FullpageHeight>
  </Box.Absolute>
))

const colorsCfg = {
  A: 'green',
  B: 'orange',
  C: 'pink',
}

const TrashPage = ({ data }) => {
  const { isMobile } = useResponsive()
  const { containerWidth } = useContext(containerWidthContext)
  // setup refs

  const colorScheme = `colors.${colorsCfg[data.recycleValue]}`
  const trashWidth = (isMobile ? 175 : 75) * (data.transform.scale ? data.transform.scale / 100 : Math.min(1, idealWidth / (data.xRange[1] - data.xRange[0])))
  const faceId = useMemo(() => random(4) + 1, [data])
  const endTransition = [
    [0 + (data.transform.mobileX || 0), -50 + (data.transform.mobileY || 0)],
    [-trashSidePos + (data.transform.x || 0), -20 + (data.transform.y || 0)],
  ]
  const endPos = [containerWidth * 1, containerWidth * 0.25]

  useLoader(data.img)

  return (
    <Box.Relative height="100vh" width="100vw" pt={theme.headerHeight} transformOrigin="0 0" transform="scale(0.5)" bg={colorScheme} onClick={() => window.open(`${process.env.PUBLIC_URL}/#/trash/${data.id}`)} cursor="pointer">
      <Box bg={colorScheme}>
        <Container>
          <Box width={responsive('100%', '50%')} mx="auto" pt={responsive('0', '5vh')}>
            <BackgroundImage src={isMobile ? shareBgMobile : shareBg} ratio={isMobile ? 750 / 574 : 1368 / 746} overflow="visible">
              <SizeMe>
                {({ size }) => (
                  <Box.Absolute left={responsive('2em', '40%')} top={responsive('8em', '36%')} transform={responsive('', 'trnsateY(-50%)')} width={responsive('55%', '45%')}>
                    <Box fontSize={`${size.width / 25 * 2}px`}>
                      <Text letterSpacing="0.05em" fontSize={responsive('1.75em', '2.25em')} fontWeight="900">＃如果你不好好回收</Text>
                      <Text fontSize={responsive('3.25em', '3.5em')} fontWeight="900" color={colorScheme}>{data.share}</Text>
                    </Box>
                  </Box.Absolute>
                  )}
              </SizeMe>
            </BackgroundImage>
          </Box>
        </Container>
        <Box
          bg={colorScheme}
          pb="2em"
          pt={responsive('6em', '2em')}
          position="relative"
        >
          <Container px="2em">
            <Text mt="0.5em" fontWeight="900" color="white" fontSize="1.5em" letterSpacing="0.1em">＃如果你想好好睡覺，你可以這麼做！</Text>
            <Handling steps={data.handling} />
          </Container>
        </Box>
        <Box.Absolute left="0" right="0" top={theme.headerHeight} height={`calc(100vh - ${theme.headerHeight})`} pointerEvents="none">
          <Container height="100%">
            <Box.Relative height="100%">
              <Box.Absolute
                width={`${trashWidth}%`}
                left={`${(100 - trashWidth) / 2}%`}
                top={responsive(`${endPos[0]}px`, `${endPos[1]}px`)}
                transform={`translate3d(${isMobile ? '10%' : 0}, -50%, 0) ${data.transform.rotate ? `rotate(${data.transform.rotate}deg)` : ''}`}
              >
                <Box transform={`translate(${endTransition[isMobile ? 0 : 1].map(d => `${d}%`).join(',')}) ${data.transform.shareScale ? `scale(${data.transform.shareScale / 100})` : ''}`}>
                  <BackgroundImage ratio={imgSize[0] / imgSize[1]} src={data.img} />
                  <Face id={faceId} />
                </Box>
              </Box.Absolute>
            </Box.Relative>
          </Container>
        </Box.Absolute>
      </Box>
      <Flex position="absolute" top="0" left="0" right="0" height={theme.headerHeight} bg={colorScheme} alignItems="center">
        <Box px={responsive('2em', '1em')}>
          <Image width="12em" src={logo} alt="回收大百科" />
        </Box>
      </Flex>
    </Box.Relative>
  )
}

export default withData(TrashPage)
